___CSS_LOADER_EXPORT___.push([module.id, ".modal-backdrop.fade{visibility:hidden}.modal-backdrop.fade.show{visibility:visible}.fade.show{display:block;z-index:1072}", "",{"version":3,"sources":["webpack://./resources/js/app/components/UploadComponent.vue"],"names":[],"mappings":"AAoJA,qBACI,iBACJ,CACA,0BACI,kBACJ,CACA,WACI,aAAc,CACd,YACJ","sourcesContent":["<template>\n    <div class=\"example-full\">\n        <div class=\"upload\">\n            <div v-for=\"file in files\" :key=\"file.id\" class=\"files\">\n                <div style=\"text-align: center\" class=\"row\">\n                    <div class=\"col-sm\">\n                        <a target=\"_blank\" :href=\"file.original_url\">{{\n                            file.name\n                        }}</a>\n                        <span v-if=\"file.error\"></span>\n                        <span v-else-if=\"file.success\">\n                            -  </span\n                        >\n                        <span v-else-if=\"file.active\"></span>\n                        <span v-else></span>\n                    </div>\n                    <div class=\"col-sm\">\n                        <a\n                            class=\"dropdown-item\"\n                            :href=\"file.original_url\"\n                            :download=\"file.name\"\n                            ></a\n                        >\n                    </div>\n\n                    <div v-if=\"!this.viewonly\" class=\"col-sm\">\n                        <a\n                            :class=\"{\n                                'dropdown-item': true,\n                                disabled:\n                                    file.active ||\n                                    file.success ||\n                                    file.error === 'compressing' ||\n                                    file.error === 'image parsing',\n                            }\"\n                            href=\"#\"\n                            @click.prevent=\"\n                                file.active ||\n                                file.success ||\n                                file.error === 'compressing'\n                                    ? false\n                                    : onEditFileShow(file)\n                            \"\n                            ></a\n                        >\n                    </div>\n                    <div v-if=\"!this.viewonly\" class=\"col-sm\">\n                        <a\n                            :class=\"{\n                                'dropdown-item': true,\n                                disabled:\n                                    file.active ||\n                                    file.success ||\n                                    file.error === 'compressing' ||\n                                    file.error === 'image parsing',\n                            }\"\n                            href=\"#\"\n                            @click.prevent=\"$refs.upload.remove(file)\"\n                            ></a\n                        >\n                    </div>\n                </div>\n            </div>\n            <div v-if=\"!this.viewonly\" class=\"example-btn\">\n                <file-upload\n                    :input-id=\"this.input_id\"\n                    :data=\"{\n                        upload_type: this.upload_type,\n                        model_id: this.model.id,\n                    }\"\n                    class=\"btn btn-primary\"\n                    post-action=\"/api/uploadClientFiles\"\n                    extensions=\"pdf,png,jpeg,webp\"\n                    accept=\"image/png,image/gif,image/jpeg,image/webp,application/pdf\"\n                    :multiple=\"true\"\n                    @input-file=\"inputFile\"\n                    @input-filter=\"inputFilter\"\n                    v-model=\"files\"\n                    ref=\"upload\"\n                >\n                    <i class=\"fa fa-plus\"></i>\n\n                     \n                </file-upload>\n            </div>\n        </div>\n\n        <div\n            :class=\"{\n                'modal-backdrop': true,\n                fade: true,\n                show: editFile.show,\n            }\"\n        ></div>\n        <div\n            :class=\"{ modal: true, fade: true, show: editFile.show }\"\n            tabindex=\"-1\"\n        >\n            <div class=\"modal-dialog modal-lg\" role=\"document\">\n                <div class=\"modal-content\">\n                    <div class=\"modal-header\">\n                        <h5 style=\"color: black\" class=\"modal-title\">\n                             \n                        </h5>\n                        <button\n                            type=\"button\"\n                            class=\"close\"\n                            @click.prevent=\"editFile.show = false\"\n                        >\n                            <span>&times;</span>\n                        </button>\n                    </div>\n                    <form @submit.prevent=\"onEditorFile\">\n                        <div class=\"modal-body\">\n                            <div class=\"form-group\">\n                                <label style=\"color: black\" for=\"name\"\n                                    >:</label\n                                >\n                                <input\n                                    type=\"text\"\n                                    class=\"form-control\"\n                                    required\n                                    id=\"name\"\n                                    placeholder=\" \"\n                                    v-model=\"editFile.name\"\n                                />\n                            </div>\n                        </div>\n                        <div class=\"modal-footer\">\n                            <button\n                                type=\"button\"\n                                class=\"btn btn-secondary\"\n                                @click.prevent=\"editFile.show = false\"\n                            >\n                                Close\n                            </button>\n                            <button type=\"submit\" class=\"btn btn-primary\">\n                                Save\n                            </button>\n                        </div>\n                    </form>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<style>\n.modal-backdrop.fade {\n    visibility: hidden;\n}\n.modal-backdrop.fade.show {\n    visibility: visible;\n}\n.fade.show {\n    display: block;\n    z-index: 1072;\n}\n</style>\n<script>\nimport FileUpload from \"vue-upload-component\";\nimport { ref } from \"vue\";\nimport useVuelidate from \"@vuelidate/core\";\nimport Util from \"Util\";\n\nexport default {\n    props: [\n        \"input_id\",\n        \"viewonly\",\n        \"model_id\",\n        \"model\",\n        \"files\",\n        \"upload_type\",\n    ],\n    components: {\n        FileUpload,\n    },\n    methods: {\n        inputFilter(newFile, oldFile, prevent) {\n            if (newFile && !oldFile) {\n                if (\n                    /(\\/|^)(Thumbs\\.db|desktop\\.ini|\\..+)$/.test(newFile.name)\n                ) {\n                    return prevent();\n                }\n                if (/\\.(php5?|html?|jsx?)$/i.test(newFile.name)) {\n                    return prevent();\n                }\n            }\n        },\n        // add, update, remove File Event\n        inputFile(newFile, oldFile) {\n            if (newFile && !oldFile) {\n                // console.log(\"add\", newFile);\n            }\n            if (newFile && oldFile) {\n                // console.log(\"update\", newFile);\n            }\n\n            if (!newFile && oldFile) {\n                this.$http\n                    .delete(\"/api/deleteClientFiles/\" + oldFile.id)\n                    .then((response) => {\n                        console.log(\"Deleted\");\n                    });\n                if (oldFile.success && oldFile.response.id) {\n                }\n            }\n            // Automatically activate upload\n            if (\n                Boolean(newFile) !== Boolean(oldFile) ||\n                oldFile.error !== newFile.error\n            ) {\n                if (this.uploadAuto && !this.$refs.upload.active) {\n                    this.$refs.upload.active = true;\n                }\n            }\n        },\n\n        onEditFileShow(file) {\n            this.editFile = { ...file, show: true };\n            this.$refs.upload.update(file, { error: \"edit\" });\n        },\n        onEditorFile() {\n            if (!this.$refs.upload.features.html5) {\n                this.alert(\"Your browser does not support\");\n                this.editFile.show = false;\n                return;\n            }\n            let data = {\n                name: this.editFile.name,\n                error: \"\",\n            };\n\n            this.$refs.upload.update(this.editFile.id, data);\n            this.editFile.error = \"\";\n            this.editFile.show = false;\n        },\n    },\n    watch: {\n        \"editFile.show\"(newValue, oldValue) {\n            if (!newValue && oldValue) {\n                let token = document\n                    .querySelector('meta[name=\"csrf-token\"')\n                    .getAttribute(\"content\");\n\n                this.$http\n                    .post(\n                        \"/api/updateClientFiles/\" + this.editFile.id,\n                        this.editFile,\n                        {\n                            \"Content-Type\": \"multipart/form-data\",\n                            \"X-CSRF-TOKEN\": token,\n                        }\n                    )\n                    .then(async (response) => {});\n\n                this.$refs.upload.update(this.editFile.id, {\n                    error: this.editFile.error || \"\",\n                });\n            }\n            if (newValue) {\n                this.$nextTick(() => {\n                    if (!this.$refs.editImage) {\n                        return;\n                    }\n                });\n            }\n        },\n    },\n    setup(props, context) {\n        const upload = ref(null);\n\n        return {\n            v$: useVuelidate(),\n            upload,\n        };\n    },\n    data() {\n        return {\n            files: [...this.files],\n            uploadAuto: true,\n            editFile: {\n                show: false,\n                name: \"\",\n            },\n        };\n    },\n};\n</script>\n"],"sourceRoot":""}]);
